export default {
  data() {
    return {
      logisticName: '',
      logisticNo: '',
      logisticText: '',
      hidden: false
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    var that = this;
    wx.request({
      url: getApp().globalData.url3 + getApp().globalData.logisticDetail + '?logisticsCode=' + options.logisticsCode + '&logisticsPostid=' + options.logisticsPostid,
      data: '',
      header: {},
      method: 'GET',
      dataType: 'json',
      responseType: 'text',
      success: function (res) {
        if (res.data.code == 200) {
          var logisticText = JSON.parse(res.data.data.logisticText);
          that.setData({
            logisticText: logisticText,
            hidden: true,
            logisticName: res.data.data.logisticName,
            logisticNo: res.data.data.logisticNo
          });

          if (logisticText.message !== 'ok') {
            wx.showToast({
              title: '快递公司正在揽件,请稍后再试',
              mask: true,
              icon: 'none'
            });
          }
        }
      },
      fail: function (res) {
        wx.showToast({
          title: '请求超时',
          mask: true
        });
      },
      complete: function (res) {}
    });
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {}
};
